@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor__imgfondo {
    background-image: url("public/build/img/fondo.jpg");
    width: 100%;
    height: 30rem;
    margin-top: -1rem;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.75);
    background-blend-mode: soft-light;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;

    .imgfondo__titulo {
        margin-bottom: 4rem;
        color: v.$blanco;
        font-weight: 100;
        font-size: 3rem;
        padding: 0 1rem;
        text-transform: capitalize;
    }
}
@include m.telefono {
    .contenedor__imgfondo {
        margin-top: 0;
        height: 40rem;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
}
@include m.tablet {
    .contenedor__imgfondo {
        height: 50rem;
    }
}
@include m.desktop {
    .contenedor__imgfondo {
        height: 60rem;
        .imgfondo__titulo {
            font-size: 3.4rem;
        }
    }
}
@include m.desktop-xl {
    .contenedor__imgfondo {
        .imgfondo__titulo {
            font-size: 4rem;
        }
    }
}
