@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
    background-color: darken(v.$gris-claro-02, 55%);
    width: 100%;
    padding: 2rem;
    text-decoration: none;
    color: v.$blanco;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    .copyright {
        font-weight: 100;
    }
    .enlaces {
        border-bottom: 1px solid v.$color-cuarto;
        padding: 2rem 0;
        display: flex;
        justify-content: space-around;
        width: 80%;
        a {
            width: 5rem;
            height: 5rem;
            border-radius: 1rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}
@include m.tablet {
    .footer {
        .enlaces {
            justify-content: center;
            a {
                &:first-of-type {
                    margin-right: 2rem;
                }
            }
        }
    }
}
