@use "base/variables" as v;
.boton {
    background-color: v.$color-principal;
    border-radius: 0.5rem;
    padding: 2rem 4rem;
    font-weight: v.$bold;
    color: v.$blanco;
    font-size: 1.8rem;
    text-transform: uppercase;
    &:hover {
        background-color: lighten(v.$color-principal, 10%);
        transform: scale(1.1);
        cursor: pointer;
        transition: background-color ease 0.3s, transform ease 0.3s;
    }
}
.rrss {
    background-color: v.$color-principal;

    .icon {
        stroke: v.$color-cuarto;
    }

    &:hover {
        .icon {
            stroke: v.$blanco;
            transition: stroke ease 0.3s;
        }
        background-color: lighten(v.$color-principal, 10%);
        transform: scale(1.1);

        cursor: pointer;
        transition: background-color ease 0.3s, transform ease 0.3s;
    }
}
.enlace-seleccionado {
    color: v.$color-principal !important;
}
.bloque-info {
    border-top: 1rem solid v.$color-principal;
    background-color: darken(v.$gris, 10%);
    color: v.$blanco;
    border-radius: 1rem;
    -webkit-box-shadow: 5px 5px 20px 0px #000000;
    box-shadow: 5px 5px 20px 0px #000000;
}
.subir {
    position: fixed;
    cursor: pointer;
    .icono {
        background-color: v.$color-principal;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 5rem;
        height: 5rem;
        position: fixed;
        bottom: 1rem;
        right: 1rem;
        z-index: 900;
        -webkit-box-shadow: 5px 5px 20px 0px #000000;
        box-shadow: 5px 5px 20px 0px #000000;
        .icon {
            stroke: v.$blanco;
        }
    }
}
