@use "base/variables" as v;
@use "base/mixins" as m;
.fondo {
    background-color: lighten(v.$color-secundario, 5%);
}
.main--productos {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.titulo-seccion {
    margin: 4rem 0 2rem 0;
    font-size: 3rem;
    font-weight: 400;
    color: darken(v.$gris, 50%);
}
.contenedor-producto {
    width: 100%;
    justify-content: center;
    margin: 2rem 0;
    height: 40rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;

    position: relative;
    &::before {
        content: "";
        width: 10rem;
        height: 2rem;
        background-color: v.$color-principal;
        position: absolute;
        bottom: -0.75rem;
        border-radius: 0.5rem;
        -webkit-box-shadow: 5px 5px 20px 0px #000000;
        box-shadow: 5px 5px 20px 0px #000000;
    }
    img {
        width: 100%;
        border-radius: 1rem;
        padding: 1rem;
    }
    h3 {
        margin: 2rem;
        font-weight: 300;
    }
    h4 {
        font-weight: 400;
    }
}
.mensaje-contacto {
    padding: 2rem 1rem;
    h2 {
        font-weight: 300;
    }
}
@include m.telefono {
    .main--productos {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 1rem;
        .contenedor-producto {
            width: 48%;

            img {
                padding: 1rem;
                margin: 1rem;
            }
            h3 {
                margin: 1rem;
                font-size: 2.2rem;
            }
            h4 {
                margin: 1rem;
                font-size: 1.8rem;
            }
        }
    }
}
@include m.tablet {
    .main--productos {
        width: 80%;
        .contenedor-producto {
            width: 32%;
        }
    }
}
@include m.desktop {
    .main--productos {
        width: 60%;
    }
}
