html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #dfe0df;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  max-width: 180rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.3rem;
}

p {
  font-family: "Roboto", sans-serif;
  font-size: 1.7rem;
}

.header {
  background-color: #0d0d0d;
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 3rem;
}
.header__logo {
  font-family: "Updock", cursive;
  font-size: 6rem;
  color: #ff6701;
  padding: 0 1rem;
  text-transform: capitalize;
}

@media (min-width: 1200px) {
  .contenedor__header-nav {
    display: flex;
    align-items: center;
  }
  .contenedor__header-nav .header {
    width: 60rem;
  }
}
.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #121212;
  margin-top: -1rem;
  padding-top: 1rem;
  padding-bottom: 2rem;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.nav__icono {
  background-color: #ff6701;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
}
.nav__icono .icon {
  stroke: #fff;
  stroke-width: 1.5;
}
.nav__menu {
  flex-direction: column;
  margin: 2rem 0;
  padding: 0;
  width: 70%;
}
.nav .ocultar {
  display: none;
}
.nav li {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 0.2rem solid #171717;
}
.nav li:last-of-type {
  border-bottom: 0.2rem solid #171717;
}
.nav a {
  font-size: 2rem;
  margin: 2rem 0;
  color: #fff;
  text-decoration: none;
  text-transform: capitalize;
}

@media (min-width: 480px) {
  .nav {
    margin: 0;
    padding: 0;
    border-radius: 0;
  }
  .nav__icono {
    display: none;
  }
  .nav__menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 0;
    width: 90%;
  }
  .nav li {
    list-style: none;
    border: none;
  }
  .nav li:last-of-type {
    border-bottom: 0;
  }
  .nav a {
    padding: 1rem 0.8rem;
    border: 1px solid transparent;
    border-radius: 1rem;
    font-size: 1.5rem;
  }
  .nav a:hover {
    border: 1px solid #ff6701;
    color: #ff6701;
    transition: border ease 0.5s, color ease 0.5s;
  }
}
@media (min-width: 768px) {
  .nav a {
    font-size: 1.8rem;
  }
}
@media (min-width: 1200px) {
  .nav {
    background-color: #0d0d0d;
    height: 11.9rem;
  }
  .nav__menu {
    justify-content: space-evenly;
  }
  .nav a {
    font-size: 1.8rem;
    padding: 2rem;
  }
}
.contenedor__imgfondo {
  background-image: url("public/build/img/fondo.jpg");
  width: 100%;
  height: 30rem;
  margin-top: -1rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.75);
  background-blend-mode: soft-light;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
}
.contenedor__imgfondo .imgfondo__titulo {
  margin-bottom: 4rem;
  color: #fff;
  font-weight: 100;
  font-size: 3rem;
  padding: 0 1rem;
  text-transform: capitalize;
}

@media (min-width: 480px) {
  .contenedor__imgfondo {
    margin-top: 0;
    height: 40rem;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}
@media (min-width: 768px) {
  .contenedor__imgfondo {
    height: 50rem;
  }
}
@media (min-width: 1024px) {
  .contenedor__imgfondo {
    height: 60rem;
  }
  .contenedor__imgfondo .imgfondo__titulo {
    font-size: 3.4rem;
  }
}
@media (min-width: 1200px) {
  .contenedor__imgfondo .imgfondo__titulo {
    font-size: 4rem;
  }
}
.section {
  margin: 12rem 0;
  height: 40rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  z-index: -10;
  position: relative;
}
.section:last-of-type {
  margin-bottom: 6rem;
}
.section::before {
  content: "";
  width: 10rem;
  height: 2rem;
  background-color: #ff6701;
  position: absolute;
  bottom: -0.75rem;
  border-radius: 0.5rem;
  -webkit-box-shadow: 5px 5px 20px 0px #000000;
  box-shadow: 5px 5px 20px 0px #000000;
}
.section .section__titulo,
.section .section__contenedor__icono,
.section .section__descripcion {
  padding: 1rem;
  margin-top: 2rem;
}
.section .section__contenedor__icono {
  background: linear-gradient(135deg, #ff6701 0%, #fea82f 50%);
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -12rem;
  border: 1rem solid #ff6701;
  -webkit-box-shadow: 5px 5px 20px 0px #000000;
  box-shadow: 5px 5px 20px 0px #000000;
}
.section .section__contenedor__icono .icon {
  width: 8rem;
  height: 8rem;
  stroke: #fff;
  stroke-width: 0.8;
}
.section .section__titulo {
  font-size: 2.5rem;
  font-weight: 700;
  margin-top: 12rem;
}
.section .section__descripcion {
  padding: 0 1.4rem;
  font-weight: 300;
}

@media (min-width: 768px) {
  .main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  .main .section {
    width: 34rem;
    margin-bottom: 4rem;
  }
  .main .section:last-of-type {
    margin: 10rem auto 4rem auto;
  }
}
@media (min-width: 1024px) {
  .main {
    justify-content: space-around;
  }
  .main .section {
    width: 30%;
  }
  .main .section:last-of-type {
    margin: 12rem 0 4rem 0;
  }
}
@media (min-width: 1200px) {
  .main {
    justify-content: space-evenly;
  }
  .main .section {
    width: 34rem;
  }
}
.footer {
  background-color: #0d0d0d;
  width: 100%;
  padding: 2rem;
  text-decoration: none;
  color: #fff;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.footer .copyright {
  font-weight: 100;
}
.footer .enlaces {
  border-bottom: 1px solid #dfe0df;
  padding: 2rem 0;
  display: flex;
  justify-content: space-around;
  width: 80%;
}
.footer .enlaces a {
  width: 5rem;
  height: 5rem;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .footer .enlaces {
    justify-content: center;
  }
  .footer .enlaces a:first-of-type {
    margin-right: 2rem;
  }
}
.boton {
  background-color: #ff6701;
  border-radius: 0.5rem;
  padding: 2rem 4rem;
  font-weight: 700;
  color: #fff;
  font-size: 1.8rem;
  text-transform: uppercase;
}
.boton:hover {
  background-color: #ff8634;
  transform: scale(1.1);
  cursor: pointer;
  transition: background-color ease 0.3s, transform ease 0.3s;
}

.rrss {
  background-color: #ff6701;
}
.rrss .icon {
  stroke: #dfe0df;
}
.rrss:hover {
  background-color: #ff8634;
  transform: scale(1.1);
  cursor: pointer;
  transition: background-color ease 0.3s, transform ease 0.3s;
}
.rrss:hover .icon {
  stroke: #fff;
  transition: stroke ease 0.3s;
}

.enlace-seleccionado {
  color: #ff6701 !important;
}

.bloque-info {
  border-top: 1rem solid #ff6701;
  background-color: #0d0f13;
  color: #fff;
  border-radius: 1rem;
  -webkit-box-shadow: 5px 5px 20px 0px #000000;
  box-shadow: 5px 5px 20px 0px #000000;
}

.subir {
  position: fixed;
  cursor: pointer;
}
.subir .icono {
  background-color: #ff6701;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  z-index: 900;
  -webkit-box-shadow: 5px 5px 20px 0px #000000;
  box-shadow: 5px 5px 20px 0px #000000;
}
.subir .icono .icon {
  stroke: #fff;
}

.fondo {
  background-color: #f89000;
}

.main--productos {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.titulo-seccion {
  margin: 4rem 0 2rem 0;
  font-size: 3rem;
  font-weight: 400;
  color: black;
}

.contenedor-producto {
  width: 100%;
  justify-content: center;
  margin: 2rem 0;
  height: 40rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
}
.contenedor-producto::before {
  content: "";
  width: 10rem;
  height: 2rem;
  background-color: #ff6701;
  position: absolute;
  bottom: -0.75rem;
  border-radius: 0.5rem;
  -webkit-box-shadow: 5px 5px 20px 0px #000000;
  box-shadow: 5px 5px 20px 0px #000000;
}
.contenedor-producto img {
  width: 100%;
  border-radius: 1rem;
  padding: 1rem;
}
.contenedor-producto h3 {
  margin: 2rem;
  font-weight: 300;
}
.contenedor-producto h4 {
  font-weight: 400;
}

.mensaje-contacto {
  padding: 2rem 1rem;
}
.mensaje-contacto h2 {
  font-weight: 300;
}

@media (min-width: 480px) {
  .main--productos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
  }
  .main--productos .contenedor-producto {
    width: 48%;
  }
  .main--productos .contenedor-producto img {
    padding: 1rem;
    margin: 1rem;
  }
  .main--productos .contenedor-producto h3 {
    margin: 1rem;
    font-size: 2.2rem;
  }
  .main--productos .contenedor-producto h4 {
    margin: 1rem;
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .main--productos {
    width: 80%;
  }
  .main--productos .contenedor-producto {
    width: 32%;
  }
}
@media (min-width: 1024px) {
  .main--productos {
    width: 60%;
  }
}
.main--despachos {
  width: 80%;
}
.main--despachos .contenedor-despachos {
  margin: 1rem 1rem 10rem 1rem;
  border-radius: 1rem;
  background-color: #0d0f13;
  -webkit-box-shadow: 5px 5px 20px 0px #000000;
  box-shadow: 5px 5px 20px 0px #000000;
}
.main--despachos .contenedor-despachos p {
  padding: 3rem 2rem;
  color: #fff;
  font-weight: 300;
  line-height: 1.5;
  text-align: center;
}

@media (min-width: 480px) {
  .main--despachos {
    width: 80%;
  }
}
@media (min-width: 768px) {
  .main--despachos {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .main--despachos {
    width: 40%;
  }
}
@media (min-width: 1200px) {
  .main--despachos {
    width: 36rem;
  }
}
.quienes-somos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.quienes-somos .contenedor-fundador {
  background-color: #0d0f13;
  color: #fff;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.quienes-somos .contenedor-fundador .imagen-fundador {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23rem;
  height: 23rem;
  margin: 1rem;
  border-radius: 50%;
  border: 1rem solid #ff6701;
}
.quienes-somos .contenedor-fundador .imagen-fundador img {
  border-radius: 50%;
}
.quienes-somos .contenedor-fundador .nombre-fundador {
  margin-top: 2rem;
}
.quienes-somos .contenedor-fundador .nombre-fundador h3 {
  font-weight: 400;
}
.quienes-somos .contenedor-fundador .descripcion-fundador p {
  font-weight: 300;
}

@media (min-width: 480px) {
  .quienes-somos {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .quienes-somos .contenedor-fundador {
    width: 25rem;
  }
  .quienes-somos .contenedor-fundador .imagen-fundador {
    width: 22rem;
    height: 22rem;
  }
}
@media (min-width: 1024px) {
  .quienes-somos {
    justify-content: center;
  }
  .quienes-somos .contenedor-fundador:first-of-type {
    margin-right: 2rem;
  }
  .quienes-somos .contenedor-fundador:last-of-type {
    margin-left: 2rem;
  }
}
.contactanos .contactanos-contenedor {
  margin: 2rem;
  background-color: #0d0f13;
  border-radius: 1rem;
  color: #fff;
}
.contactanos .contactanos-contenedor .bloque {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
.contactanos .contactanos-contenedor .bloque label {
  font-weight: 300;
  padding-bottom: 1rem;
}
.contactanos .contactanos-contenedor .bloque input,
.contactanos .contactanos-contenedor .bloque textarea {
  padding: 1rem;
}
.contactanos .contactanos-contenedor .bloque textarea {
  resize: none;
  width: 100%;
  height: 10rem;
}
.contactanos .contactanos-contenedor .bloque--boton input {
  border: none;
  padding: 2rem 1rem;
}

@media (min-width: 480px) {
  .contactanos .bloque--boton input {
    width: 40%;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .contactanos {
    margin: 0 auto;
  }
  .contactanos .contactanos-contenedor {
    width: 69rem;
  }
}