@use "base/variables" as v;
@use "base/mixins" as m;

.section {
    margin: 12rem 0;
    height: 40rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    position: relative;
    z-index: -10;
    position: relative;
    &:last-of-type {
        margin-bottom: 6rem;
    }
    &::before {
        content: "";
        width: 10rem;
        height: 2rem;
        background-color: v.$color-principal;
        position: absolute;
        bottom: -0.75rem;
        border-radius: 0.5rem;
        -webkit-box-shadow: 5px 5px 20px 0px #000000;
        box-shadow: 5px 5px 20px 0px #000000;
    }
    .section__titulo,
    .section__contenedor__icono,
    .section__descripcion {
        padding: 1rem;
        margin-top: 2rem;
    }
    .section__contenedor__icono {
        //background-color: v.$color-secundario;
        background: linear-gradient(135deg, rgba(255, 103, 1, 1) 0%, rgba(254, 168, 47, 1) 50%);
        border-radius: 50%;
        width: 20rem;
        height: 20rem;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: -12rem;
        border: 1rem solid v.$color-principal;
        -webkit-box-shadow: 5px 5px 20px 0px #000000;
        box-shadow: 5px 5px 20px 0px #000000;

        .icon {
            width: 8rem;
            height: 8rem;
            stroke: v.$blanco;
            stroke-width: 0.8;
        }
    }
    .section__titulo {
        font-size: 2.5rem;
        font-weight: 700;
        margin-top: 12rem;
    }
    .section__descripcion {
        padding: 0 1.4rem;
        font-weight: 300;
    }
}
@include m.tablet {
    .main {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        .section {
            width: 34rem;
            margin-bottom: 4rem;
            &:last-of-type {
                margin: 10rem auto 4rem auto;
            }
        }
    }
}
@include m.desktop {
    .main {
        justify-content: space-around;
        .section {
            width: 30%;
            //max-width: 60rem;
            &:last-of-type {
                margin: 12rem 0 4rem 0;
            }
        }
    }
}
@include m.desktop-xl {
    .main {
        justify-content: space-evenly;
        .section {
            width: 34rem;
        }
    }
}
