@use "base/variables" as v;
@use "base/mixins" as m;

.header {
    //margin-top: 5rem;
    background-color: darken(v.$gris-claro-02, 55%);
    width: 100%;
    padding-top: 2rem;
    padding-bottom: 3rem;
    &__logo {
        font-family: v.$fuente_titulo;
        font-size: 6rem;
        color: v.$color-principal;
        padding: 0 1rem;
        text-transform: capitalize;
    }
}

@include m.desktop-xl {
    .contenedor__header-nav {
        display: flex;
        align-items: center;
        .header {
            width: 60rem;
        }
    }
}
