@use "base/variables" as v;
@use "base/mixins" as m;

.contactanos {
    .contactanos-contenedor {
        margin: 2rem;
        background-color: darken(v.$gris, 10%);
        border-radius: 1rem;
        color: v.$blanco;
        .bloque {
            display: flex;
            flex-direction: column;
            padding: 2rem;
            label {
                font-weight: 300;
                padding-bottom: 1rem;
            }
            input,
            textarea {
                padding: 1rem;
            }
            textarea {
                resize: none;
                width: 100%;
                height: 10rem;
            }
        }
        .bloque--boton {
            input {
                border: none;
                padding: 2rem 1rem;
            }
        }
    }
}
@include m.telefono {
    .contactanos {
        .bloque--boton {
            input {
                width: 40%;
                margin: 0 auto;
            }
        }
    }
}
@include m.tablet {
    .contactanos {
        margin: 0 auto;
        .contactanos-contenedor {
            width: 69rem;
        }
    }
}
