@use "base/variables" as v;
@use "base/mixins" as m;

.main--despachos {
    width: 80%;
    .contenedor-despachos {
        margin: 1rem 1rem 10rem 1rem;
        border-radius: 1rem;
        background-color: darken(v.$gris, 10%);
        -webkit-box-shadow: 5px 5px 20px 0px #000000;
        box-shadow: 5px 5px 20px 0px #000000;
        p {
            padding: 3rem 2rem;
            color: v.$blanco;
            font-weight: 300;
            line-height: 1.5;
            text-align: center;
        }
    }
}
@include m.telefono {
    .main--despachos {
        width: 80%;
    }
}
@include m.tablet {
    .main--despachos {
        width: 60%;
    }
}
@include m.desktop {
    .main--despachos {
        width: 40%;
    }
}
@include m.desktop-xl {
    .main--despachos {
        width: 36rem;
    }
}
