@use "base/variables" as v;
@use "base/mixins" as m;

.nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: darken(v.$gris-claro-02, 53%);
    margin-top: -1rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;

    &__icono {
        background-color: v.$color-principal;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        .icon {
            stroke: v.$blanco;
            stroke-width: 1.5;
        }
    }
    &__menu {
        flex-direction: column;
        margin: 2rem 0;
        padding: 0;
        width: 70%;
    }
    .ocultar {
        display: none;
    }
    li {
        display: flex;
        justify-content: center;
        align-items: center;
        border-top: 0.2rem solid darken(v.$gris-claro-02, 51%);
        &:last-of-type {
            border-bottom: 0.2rem solid darken(v.$gris-claro-02, 51%);
        }
    }
    a {
        font-size: 2rem;
        margin: 2rem 0;
        color: v.$blanco;
        text-decoration: none;
        text-transform: capitalize;
    }
}

@include m.telefono {
    .nav {
        margin: 0;
        padding: 0;
        border-radius: 0;
        &__icono {
            display: none;
        }
        &__menu {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 0;
            margin: 0;
            width: 90%;
        }
        li {
            list-style: none;
            border: none;
            &:last-of-type {
                border-bottom: 0;
            }
        }
        a {
            padding: 1rem 0.8rem;
            border: 1px solid transparent;
            border-radius: 1rem;
            font-size: 1.5rem;
            &:hover {
                border: 1px solid v.$color-principal;
                color: v.$color-principal;
                transition: border ease 0.5s, color ease 0.5s;
            }
        }
    }
}
@include m.tablet {
    .nav {
        a {
            font-size: 1.8rem;
        }
    }
}
@include m.desktop-xl {
    .nav {
        background-color: darken(v.$gris-claro-02, 55%);
        height: 11.9rem;
        &__menu {
            justify-content: space-evenly;
        }
        a {
            font-size: 1.8rem;
            padding: 2rem;
        }
    }
}
