@use "base/variables" as v;
@use "base/mixins" as m;
.quienes-somos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    .contenedor-fundador {
        background-color: darken(v.$gris, 10%);
        color: v.$blanco;
        padding: 2rem;
        border-radius: 1rem;
        margin-bottom: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        flex-direction: column;
        .imagen-fundador {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 23rem;
            height: 23rem;
            margin: 1rem;
            border-radius: 50%;
            border: 1rem solid v.$color-principal;
            img {
                border-radius: 50%;
            }
        }
        .nombre-fundador {
            margin-top: 2rem;
            h3 {
                font-weight: 400;
            }
        }
        .descripcion-fundador {
            p {
                font-weight: 300;
            }
        }
    }
}
@include m.telefono {
    .quienes-somos {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        .contenedor-fundador {
            width: 25rem;
            .imagen-fundador {
                width: 22rem;
                height: 22rem;
            }
        }
    }
}
@include m.desktop {
    .quienes-somos {
        justify-content: center;
        .contenedor-fundador {
            &:first-of-type {
                margin-right: 2rem;
            }
            &:last-of-type {
                margin-left: 2rem;
            }
        }
    }
}
