@use "variables" as v;
@use "mixins" as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
    scroll-behavior: smooth;
}

body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    background-color: v.$color-cuarto;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
.contenedor {
    width: 90%;
    max-width: 180rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
